.App {
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  width: 100vw
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

div.foreground {
  width: 100%;
}

div.open-button {
  padding: 5px;
  height: 100vh; 
  display: -ms-flexbox; 
  display: flex; 
  -ms-flex-pack: start; 
      justify-content: flex-start;
  background-color: #ffffff;
}

img.open-button {
  height: 25px; 
  width: 25px; 
  -webkit-transform: rotate(180deg); 
      -ms-transform: rotate(180deg); 
          transform: rotate(180deg);
}

div.close-button {
  padding: 5px; 
  display: -ms-flexbox; 
  display: flex; 
  -ms-flex-pack: end; 
      justify-content: flex-end
}

img.close-button {
  height: 25px; 
  width: 25px;
}

img.sort-button-asc {
  height: 8px; 
  width: 8px;
  padding: 5px;
  vertical-align: sub;
  -webkit-transform: rotate(180deg);
      -ms-transform: rotate(180deg);
          transform: rotate(180deg);
}

img.sort-button-desc {
  height: 8px; 
  width: 8px;
  padding: 5px;
  vertical-align: sub;
}

img.sort-button-default {
  height: 8px; 
  width: 8px;
  padding: 5px;
  vertical-align: sub;
}

h5, h1 {
  margin: 0;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



